import { useSelector } from "react-redux";
import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { PureComponent } from "react";
import * as colors from "./../../colors/colors";
// import { LineChart, Line, XAxis, YAxis, CartesianGrid, CartesianAxis, Tooltip, Legend, ResponsiveContainer } from 'recharts';

import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Line } from "react-chartjs-2";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

export default function FourthTest() {
  const { isAuth, currentUser, data, dataAvg } = useSelector(
    (state) => state.auth
  );
  let val = data[0]["-4"];

  //   
  const avg = dataAvg.data;
  const low = avg.filter((i) => i[""] === "  ");
  const middle = avg.filter(
    (i) => i[""] === "  "
  );
  const hight = avg.filter((i) => i[""] === "  ");
  const hightTop = +middle[0][""] + +low[0][""] - +low[0][""];

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: "bottom",
      },
      title: {
        display: true,
        text: " 4  ",
      },
    },
  };
  const labels = ["", " ", " ", ""];
  const list = {
    labels,
    datasets: [
      {
        label: " ",
        data: [NaN, val, val, NaN],
        borderColor: colors.orange,
        backgroundColor: colors.orange,
      },
      {
        label: "  ",
        data: [NaN, low[0][""], low[0][""], NaN],
        borderColor: colors.blueLow,
        backgroundColor: colors.blueLow,
      },
      {
        label: "  ",
        data: [NaN, middle[0][""], middle[0][""], NaN],
        borderColor: colors.blue,
        backgroundColor: colors.blue,
      },
      {
        label: "  ",
        data: [NaN, hight[0][""], hightTop, NaN],
        borderColor: colors.blueHight,
        backgroundColor: colors.blueHight,
      },
    ],
  };

  return (
    <Box className="container-diagrams"
    sx={{
        minHeight: { xs: '300px' },
      }}>
      <Line options={options} data={list} />
    </Box>
  );
}
