import { useSelector } from "react-redux";
import { Box } from "@mui/system";

import { Radar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

export default function FirstTest() {
  const { isAuth, currentUser, data, dataAvg } = useSelector(
    (state) => state.auth
  );
  let val = data[0];
  //   

  //  
  const legend = [
    " ",
    "   ",
    "   ",
    "   ",
    "   ",
    "    ",
    "     ",
  ];

  const legendLabels = [
    " ",
    "  ",
    "  ",
    "  ",
    "  ",
    "   ",
    "  ",
  ];

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: " 1   ",
      },
    },
  };

  const list = {
    labels: legendLabels,
    datasets: [
      {
        label: " ",
        data: legend.map((i) => val[i]),
        borderColor: "rgba(230, 145, 28, 0.8)",
        backgroundColor: "rgba(230, 145, 28, 0.1)",
      },
      {
        label: "    ",
        data: legend.map((i) =>
          parseFloat(dataAvg.data[0][i].replace(",", ".").replace(" ", ""))
        ),
        borderColor: "rgba(53, 162, 235, 0.8)",
        backgroundColor: "rgba(53, 162, 235, 0.1)",
      },
    ],
  };

  const drowDiagram = () => {
    if (dataAvg.data[0]) {
      return <Radar options={options} data={list} />;
    } else {
      return null;
    }
  };

  return (
    <Box
      className="container-diagrams"
      sx={{
        marginTop: "50px",
      }}
    >
      {drowDiagram()}
    </Box>
  );
}
